<script setup lang="ts">
import { getAccessLabels, getDurationLabel, getPriceLabel, getReleaseDateTitle } from "~/utils/subscription";

export interface AllSubscriptionsProps {
  subscriptions?: IModelCommerce[];
  disabled?: boolean;
  hideOnNoContent?: boolean;
  noContentText?: string;
}

const emit = defineEmits(["buyCommerce"]);
const { t } = useI18n();
const props = defineProps<AllSubscriptionsProps>();

const onClickBuyCommerce = (commerceOption: IModelCommerce) => emit("buyCommerce", commerceOption);
</script>

<template>
  <BaseList
    :items="props.subscriptions"
    grid-class="flex flex-col gap-6"
    :no-content-text="props.noContentText"
    :hide-on-no-content="props.hideOnNoContent"
  >
    <template #start>
      <slot name="start" />
    </template>
    <template #item="{ item }">
      <CardSubscriptionNew
        :title="item.title"
        :disabled="props.disabled || item.purchaseProcessing"
        :button-label="item.purchaseProcessing ? t('pages.account_billing.payment_in_progress') : undefined"
        :description="item.description"
        :price="getPriceLabel(item.price, item.currency)"
        :active="item.isActive"
        :duration-label="getDurationLabel(item)"
        :access-labels="getAccessLabels(item.type)"
        :release-date-title="getReleaseDateTitle(item.startDate)"
        :allowed-access-identity-items="item.allowedAccessIdentityItems"
        :is-region-available="item.isEventAvailableInUserRegion"
        :region-unavailable-message="t('labels.subscription_geo_blocked_label')"
        @click-subscribe="onClickBuyCommerce(item)"
      />
    </template>
  </BaseList>
</template>
